import React from "react";
import { ContentLayoutWithSidebar } from "../../components/Layout/ContentLayout";
import { SideBar } from "../../components/SideBar/SideBar";
import { SideBarQuickstart } from "../../components/SideBar/SideBarQuickstart";
import { SideBarHelp } from "../../components/SideBar/SideBarHelp";
import SideBarCategories from "../../components/SideBar/SideBarCategories";
import { IGatsbyLocation } from '../../interfaces';
import { SubscriptionService } from '../../services/SubscriptionService';
import { IHeaderProps } from "../../components/Layout/Header";
import { Link } from "gatsby";

export class VoucherPage extends React.Component<{location: IGatsbyLocation}, {code: string, error: string, hasQueryVoucher: boolean}>
{
  constructor(props)
  {
    super(props);
    this.state = {
      code: null,
      error: null,
      hasQueryVoucher: false
    };
  }

  onSubscribe = () => {
    SubscriptionService.openForm();
  }

  handleChange = (ev) => {
    this.setState({
      code: ev.target.value,
    })
  }

  onRedeem = (ev) => {
    ev.preventDefault();
    if(this.state.code == null || this.state.code.trim() == "")
    {
      this.setState({
        error: "Bitte geben sie einen validen Gutscheincode ein."
      })
    }
    else
    {
      let today = new Date();
      let voucher = this.state.code.toUpperCase();
      voucher = voucher + today.getFullYear() + "_" +  ("0" + (today.getMonth()+1)).slice(-2);
      let link = 'https://www.udemy.com/einfuehrung-in-power-pivot/?couponCode=' + voucher;
      window.open(link,'_blank');
      this.setState({
        error: null
      })
    }
  }
  
  componentDidMount()
  {
    if(typeof window !== "undefined")
    {
      let search = (this.props.location as any).search.substr(1);
      let params: any = {}
      for(let p of search.split("&"))
      {
        let s = p.split("=");
        if(s.length == 2)
        {
          params[s[0]] = s[1];
        }
      }
      if(params.gutscheincode != null || params.coupon != null)
      {
        let val = params.gutscheincode || params.coupon;
        (document.getElementById("redeem-input") as HTMLInputElement).value = val;
        this.setState({
          code: val,
          hasQueryVoucher: true
        })
      }
    }
  }

  render()
  {
    let header: IHeaderProps = {
      description: "Artikel, Tipps&Tricks, Online Kurse zu Excel Power Pivot, Power BI und Power Query | Gutschein für kostenlose Kurse einlösen",
      title: "Home | Powerpivot Insights | Durchblick durch Daten",
      type: "website",
      url: "gutschein"
    }
    return (
      <ContentLayoutWithSidebar 
      header={header}
      sidebar={(
        <SideBar isFixed={true}>
          <SideBarQuickstart></SideBarQuickstart>
          <SideBarHelp></SideBarHelp>
          <SideBarCategories></SideBarCategories>
        </SideBar>
      )} 
      >
      <div className="section">
        <h1>Dein Gutschein</h1>        
        <p>Super! Du hast einen Gutschein für unseren Online-Kurs erhalten!</p>
        <p>In unserem Online-Kurs "Einführung in Power Pivot" zeigen wir dir, was du mit Excel Power Pivot machen kannst. Wenn du mehr wissen willst, findest du hier auf unserem Blog viele Artikel zu den Themen Excel Power Pivot, Power Query und Power BI. Melde dich zu unserem Newsletter an und du verpasst keinen Artikel.</p>
        <div style={{textAlign: "center"}}>
          <button className="button action-button" onClick={this.onSubscribe}>Zum Newsletter anmelden</button>
        </div>
      </div>
      <div className="section">
        <form onSubmit={this.onRedeem}>
          <h1>Gutschein einlösen</h1>
          {
            this.state.hasQueryVoucher == false ? 
              <p>Tippe hier den Gutscheincode ein und wir leiten dich direkt zum Anbieter weiter</p> 
              :
              <p>Löse deinen Gutschein ein und wir leiten dich direkt zum Anbieter weiter</p> 
          }
          <div>
            <div className="input-label">
              <label>Gutscheincode</label>
              {
                (this.state.error != null) ? <span className="error-string">{this.state.error}</span> : ""
              }
            </div>
            <input id="redeem-input" onChange={this.handleChange} name="gutscheincode" className="input" type="text"></input>
          </div>
          <div style={{textAlign: "center", marginTop:".5em"}}>
            <input type="submit" className="button action-button" value="meinen Gutschein einlösen"/>
          </div>
          <div style={{marginTop: "15px"}}>
            <p><b>Probleme mit dem Gutschein?</b> <Link to="/kontakt">Bitte kurze Nachricht an uns!</Link></p>
          </div>
        </form>
      </div>
      </ContentLayoutWithSidebar>
    )
  }
}

const Page = ({data, pageContext, location}) => {
  return (
    <VoucherPage location={location}></VoucherPage>
  );
};
export default Page;


